  components: {
    CnTable,
    CheckboxHeader,
    CheckboxBody,
    TextCaption,
    SearchCaption,
    FilterHeader,
    OperationBody,
  },
  data() {
    return {
      dataSource: [],
      columns: [],
      isLoading: false,
    };
  },
  methods: {
    updateCol: function(col = {}) {
      this.dataSource = this.dataSource.map((item) => {
        if (item.id === col.id) {
          return col;
        }
        return item;
      });
    },

    updateRow: function(dataSource = []) {
      this.dataSource = dataSource;
    },
    handleAction: function(action = function() {}, ...v) {
      return action.apply(this, v);
    },
    handleBoundaryTop: function() {
      console.log("reach top");
    },
    handleBoundaryBottom: function() {
      console.log("reach bottom");
      this.fetchData();
    },
    createDataSource: function(size = 100) {
      let dataSource = [];
      let step = Math.ceil(this.dataSource.length / size);
      for (let i = 0; i < size; i++) {
        let item = defautlData[Math.floor(Math.random() * defautlData.length)];
        dataSource.push({
          ...item,
          ...{
            index: `${step}-${i}`,
            id:
              Date.now().toString(36) +
              Math.random()
                .toString(36)
                .slice(2),
          },
        });
      }
      return dataSource;
    },
    fetchData: function() {
      const dataSource = this.dataSource;
      const self = this;
      setTimeout(function() {
        self.dataSource = [...dataSource, ...self.createDataSource()];
        self.isLoading = false;
      }, 1000);
      this.isLoading = true;
    },
    toggelFullscreen: function toggleFullScreen() {
      const cnTable = this.$refs["cnTable"].$el;
      if (screenfull.isEnabled) {
        screenfull.toggle(cnTable);
      }
    },
    createColumns: function() {
      this.columns = [
        {
          title: "No.",
          key: "index",
          width: 100,
          fixed: "left",
          header: {
            slot: "CheckboxHeader",
            action: function(done = function() {}) {
              const data = this.dataSource;
              this.updateRow(data.map(done));
            },
          },
          body: {
            slot: "CheckboxBody",
            action: function(v) {
              this.updateCol(v);
            },
          },
        },
        {
          title: "姓名",
          key: "name",
          fixed: "left",
          width: 150,
          caption: {
            slot: "SearchCaption",
            action: function(done = function() {}) {
              const dataSource = this.createDataSource();
              this.dataSource = dataSource.filter(done);
            },
          },
        },
        {
          title: "年龄",
          key: "age",
          width: 100,
          header: {
            slot: "FilterHeader",
            actions: {
              up: function(done = function() {}) {
                const data = this.dataSource;
                this.updateRow(data.sort(done));
              },
              down: function(done = function() {}) {
                const data = this.dataSource;
                this.updateRow(data.sort(done));
              },
            },
          },
          caption: {
            slot: "TextCaption",
            text: "Bonus Items",
          },
        },
        {
          title: "省份",
          key: "province",
