          key: "zip",
          width: 100,
        },
        {
          title: "操作",
          key: "action",
          width: 150,
          body: {
            slot: "OperationBody",
            actions: {
              update: function(v = {}) {
                console.log("update");
              },
              delete: function(v = {}) {
                const data = this.dataSource;
                const i = data.findIndex((i) => i.id === v.id);
                data.splice(i, 1);
              },
            },
          },
        },
      ];
    },
